import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import '../assets/css/AdminSidebar.css';

const adminSections = [
  { path: '/admin/dashboard', label: '總覽' },
  { path: '/admin/qa', label: '問答管理' },
  { path: '/admin/user-management', label: '用戶管理' },
  { path: '/admin/feedback-management', label: '反饋管理' }, 
  // Add more sections here as needed
];

export default function AdminSidebar({ onLinkClick }) {
  const location = useLocation();
  return (
    <nav className="admin-sidebar">
      <ul>
        {adminSections.map(section => (
          <li key={section.path} className={location.pathname === section.path ? 'active' : ''}>
            <Link to={section.path} onClick={onLinkClick}>{section.label}</Link>
          </li>
        ))}
      </ul>
    </nav>
  );
}
