import React, { useState, useEffect } from "react";
import "../assets/css/DocumentManagement.css";

function DocumentManagement() {
  const [files, setFiles] = useState([]);
  const [uploading, setUploading] = useState(false);

  // 取得已上傳檔案列表
  const fetchFiles = async () => {
    try {
      const res = await fetch('/api/files');
      const data = await res.json();
      if (data.success) setFiles(data.files);
      else setFiles([]);
    } catch {
      setFiles([]);
    }
  };

  useEffect(() => {
    fetchFiles();
  }, []);

  // 上傳檔案
  const handleFileUpload = async (event) => {
    const file = event.target.files[0];
    if (!file) return;
    setUploading(true);
    const formData = new FormData();
    formData.append('file', file);
    // 不再手動附加 uploader，改由後端從 JWT 取得
    try {
      const token = localStorage.getItem('token'); // 假設登入時已存入 localStorage
      const res = await fetch('/api/upload', {
        method: 'POST',
        headers: token ? { 'Authorization': 'Bearer ' + token } : {},
        body: formData
      });
      const data = await res.json();
      if (data.success) {
        fetchFiles();
        alert('上傳成功');
      } else {
        alert(data.error || '上傳失敗');
      }
    } catch {
      alert('上傳失敗');
    }
    setUploading(false);
  };

  return (
    <div className="document-management">
      <h1>文檔管理</h1>
      <div className="upload-section">
        <input
          type="file"
          onChange={handleFileUpload}
          className="file-input"
          disabled={uploading}
        />
      </div>
      <div className="file-list">
        <h2>已上傳文件</h2>
        <ul>
          {files.map((file, index) => (
            <li key={index} className="file-item">
              <span className="file-name">{file.filename}</span>
              <span style={{ marginLeft: 12, color: '#888', fontSize: 14 }}>
                上傳者：{file.uploader || '未知'}
              </span>
              <span style={{ marginLeft: 12, color: '#888', fontSize: 14 }}>
                {file.upload_time ? new Date(file.upload_time).toLocaleString() : ''}
              </span>
              <a
                className="download-btn"
                href={`/api/download/${encodeURIComponent(file.filename)}`}
                download={file.filename}
                style={{ marginLeft: 12 }}
              >
                下載
              </a>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}

export default DocumentManagement;
