import React, { useState } from "react";
import { Route, Switch, useLocation } from "react-router-dom";
import "./assets/css/App.css";
import "./assets/css/Sidebar.css";
import "./assets/css/Layout.css";
import "./assets/css/DocumentManagement.css";
import "./assets/css/KnowledgeBase.css";
import Sidebar from "./components/Sidebar";
import KnowledgeBase from "./components/KnowledgeBase";
import DocumentManagement from "./components/DocumentManagement";
import Collaboration from "./components/Collaboration";
import DataAnalysis from "./components/DataAnalysis";
import Project from "./components/Project";
import Feedback from './components/Feedback';
import Auth from "./components/Auth";
import AdminLogin from "./components/AdminLogin";
import AdminDashboard from "./components/AdminDashboard";
import AdminQA from "./components/AdminQA";
import AdminSidebar from "./components/AdminSidebar";
import AdminUserManagement from "./components/AdminUserManagement";
import AdminFeedbackManagement from "./components/AdminFeedbackManagement";
import { jwtDecode } from "jwt-decode";

function App() {
  const [token, setToken] = useState(() => localStorage.getItem('token') || '');
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  const [user] = useState('');

  const toggleSidebar = () => setIsSidebarOpen((prev) => !prev);
  const closeSidebar = () => setIsSidebarOpen(false);

  const handleSetToken = (newToken) => {
    setToken(newToken);
    localStorage.setItem('token', newToken);
    try {
      const decoded = jwtDecode(newToken);
      if (decoded && (decoded.isAdmin === true || decoded.role === 'admin')) {
        window.location.replace('/GPT-Knowledge-System/#/admin/dashboard');
      } else {
        window.location.replace('/GPT-Knowledge-System/#/');
      }
    } catch (e) {
      window.location.replace('/GPT-Knowledge-System/#/');
    }
  };

  const handleLogout = () => {
    setToken('');
    localStorage.removeItem('token');
  };

  const location = useLocation();

  // 暫時關閉登入驗證，所有人都能直接進入主畫面
  // if (!token) {
  //   if (location.pathname !== '/admin/login' && location.pathname !== '/login') {
  //     window.location.hash = '#/login';
  //     return null;
  //   }
  //   if (location.pathname === '/admin/login') {
  //     return <AdminLogin setAdminToken={handleSetToken} setAdminUser={() => {}} />;
  //   }
  //   return <Auth setToken={handleSetToken} />;
  // }

  let isAdmin = false;
  try {
    const decoded = jwtDecode(token);
    isAdmin = decoded && (decoded.isAdmin === true || decoded.role === 'admin');
  } catch (err) {
    isAdmin = false;
  }

  if (isAdmin && !location.pathname.startsWith('/admin')) {
    window.location.replace('#/admin/dashboard');
    return null;
  }
  if (!isAdmin && location.pathname.startsWith('/admin')) {
    window.location.replace('#/');
    return null;
  }

  if (isAdmin) {
    return (
      <>
        <button className="hamburger" onClick={toggleSidebar}>☰</button>
        <button onClick={handleLogout} style={logoutButtonStyle}>登出</button>
        <div className="app-container">
          <AdminSidebar />
          <div className="content" style={{ marginLeft: 200 }}>
            <Switch>
            <Route exact path="/admin/dashboard" component={AdminDashboard} />
            <Route path="/admin/qa" render={() => <AdminQA adminToken={token} />} />
            <Route path="/admin/user-management" component={AdminUserManagement} />
            <Route path="/admin/feedback-management" component={AdminFeedbackManagement} />
          </Switch>
          </div>
        </div>
      </>
    );
  }

  return (
    <>
      <button className="hamburger" onClick={toggleSidebar}>☰</button>
      <button onClick={handleLogout} style={logoutButtonStyle}>登出</button>
      <div className="app-container">
        <Sidebar isOpen={isSidebarOpen} onLinkClick={closeSidebar} />
        <div className="content">
          <Switch>
            <Route exact path="/" component={Project} />
            <Route path="/knowledge" component={KnowledgeBase} />
            <Route path="/documents" render={() => <DocumentManagement user={user} />} />
            <Route path="/collaboration" component={Collaboration} />
            <Route path="/analytics" component={DataAnalysis} />
            <Route path="/feedback" component={Feedback} />
          </Switch>
        </div>
      </div>
    </>
  );
}

const logoutButtonStyle = {
  position: "absolute",
  top: 10,
  right: 10,
  zIndex: 1000,
  background: "#f44336",
  color: "#fff",
  border: "none",
  borderRadius: "6px",
  padding: "6px 16px",
  cursor: "pointer"
};

export default App;
