import React, { useState } from "react";
import axios from "axios";

const Feedback = () => {
  const [feedback, setFeedback] = useState("");
  const [status, setStatus] = useState("");

  // 取得當前用戶名稱（若有登入系統）
  const username = localStorage.getItem("username") || "anonymous";

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!feedback.trim()) {
      setStatus("請輸入您的反饋內容。");
      return;
    }
    try {
      await axios.post("/api/feedback", {
        username,
        feedback: feedback.trim(),
      });
      setStatus("感謝您的反饋！");
      setFeedback("");
    } catch (err) {
      setStatus("送出失敗，請稍後再試。");
    }
  };

  return (
    <div style={{ maxWidth: 600, margin: "0 auto", padding: 24 }}>
      <h2 style={{ textAlign: "center" }}>反饋</h2>
      <form onSubmit={handleSubmit}>
        <textarea
          value={feedback}
          onChange={(e) => setFeedback(e.target.value)}
          placeholder="請輸入您的反饋..."
          rows={6}
          style={{ width: "100%", marginBottom: 16, resize: "vertical" }}
        />
        <div style={{ textAlign: "center" }}>
          <button type="submit" style={{ background: "#4caf50", color: "#fff", padding: "8px 24px", border: "none", borderRadius: 4 }}>
            送出反饋
          </button>
        </div>
      </form>
      {status && <div style={{ textAlign: "center", color: status.includes("感謝") ? "green" : "red", marginTop: 12 }}>{status}</div>}
    </div>
  );
};

export default Feedback;
