import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import '../assets/css/AdminUserManagement.css';

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000';

export default function AdminUserManagement() {
  const history = useHistory();
  const [users, setUsers] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const [showAddUser, setShowAddUser] = useState(false);
  const [newUser, setNewUser] = useState({
    name: '',
    password: '',
    role: 'user'
  });
  const [editingUser, setEditingUser] = useState(null);
  const [editForm, setEditForm] = useState({ name: '', password: '', role: 'user' });

  const token = localStorage.getItem('token');

  // Fetch users from backend
  useEffect(() => {
    const fetchUsers = async () => {
      setIsLoading(true);
      try {
        const token = localStorage.getItem('token');
        if (!token) {
          console.error('No token found');
          setError('Authentication required');
          return;
        }

        console.log('Fetching users with token:', token);
        const res = await fetch(`${API_URL}/api/users`, {
          headers: { 
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          }
        });
        
        console.log('Response status:', res.status);
        const data = await res.json();
        console.log('Response data:', data);
        
        if (res.status === 403) {
          setError('You do not have permission to access this page');
          return;
        }
        
        if (data.success) {
          setUsers((data.users || []).map(u => ({
            id: u._id,
            name: u.username,
            role: u.role || (u.isAdmin ? 'admin' : 'user')
          })));
        } else {
          console.error('Error in response:', data.error);
          setError(data.error || 'Failed to fetch users');
        }
      } catch (err) {
        console.error('Fetch error:', err);
        setError('Failed to fetch users: ' + err.message);
      } finally {
        setIsLoading(false);
      }
    };
    fetchUsers();
  }, [history, token]);

  const handleSearch = (e) => {
    setSearchTerm(e.target.value);
  };

  const filteredUsers = users.filter(user =>
    user.name.toLowerCase().includes(searchTerm.toLowerCase())
  );

  // Add user
  const handleAddUser = async (e) => {
    e.preventDefault();
    try {
      setIsLoading(true);
      const res = await fetch(`${API_URL}/api/register`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({ username: newUser.name, password: newUser.password })
      });
      const data = await res.json();
      if (res.ok && data.userId) {
        // Set role if needed
        if (newUser.role === 'admin') {
          await fetch(`${API_URL}/api/users/${data.userId}`, {
            method: 'PATCH',
            headers: {
              'Content-Type': 'application/json',
              'Authorization': `Bearer ${token}`
            },
            body: JSON.stringify({ role: 'admin' })
          });
        }
        setUsers([...users, { id: data.userId, name: newUser.name, role: newUser.role }]);
        setNewUser({ name: '', password: '', role: 'user' });
        setShowAddUser(false);
      } else {
        setError(data.error || 'Failed to add user');
      }
    } catch (err) {
      setError('Failed to add user');
    } finally {
      setIsLoading(false);
    }
  };

  // Delete user
  const handleDeleteUser = async (userId) => {
    if (window.confirm('Are you sure you want to delete this user?')) {
      try {
        setIsLoading(true);
        const res = await fetch(`${API_URL}/api/users/${userId}`, {
          method: 'DELETE',
          headers: { 'Authorization': `Bearer ${token}` }
        });
        const data = await res.json();
        if (data.success) {
          setUsers(users.filter(user => user.id !== userId));
        } else {
          setError(data.error || 'Failed to delete user');
        }
      } catch (err) {
        setError('Failed to delete user');
      } finally {
        setIsLoading(false);
      }
    }
  };

  // Edit user
  const handleEditClick = (user) => {
    setEditingUser(user);
    setEditForm({ name: user.name, password: '', role: user.role });
  };

  const handleEditInputChange = (e) => {
    const { name, value } = e.target;
    setEditForm(prev => ({ ...prev, [name]: value }));
  };

  const handleEditSubmit = async (e) => {
    e.preventDefault();
    try {
      setIsLoading(true);
      const body = { role: editForm.role };
      if (editForm.name !== editingUser.name) body.name = editForm.name;
      if (editForm.password) body.password = editForm.password;
      const res = await fetch(`${API_URL}/api/users/${editingUser.id}`, {
        method: 'PATCH',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(body)
      });
      const data = await res.json();
      if (data.success) {
        setUsers(users.map(user =>
          user.id === editingUser.id
            ? { ...user, name: editForm.name, role: editForm.role }
            : user
        ));
        setEditingUser(null);
        setEditForm({ name: '', password: '', role: 'user' });
      } else {
        setError(data.error || 'Failed to update user');
      }
    } catch (err) {
      setError('Failed to update user');
    } finally {
      setIsLoading(false);
    }
  };

  const handleEditCancel = () => {
    setEditingUser(null);
    setEditForm({ name: '', password: '', role: 'user' });
  };

  // Toggle admin role
  const handleToggleAdmin = async (userId, currentRole) => {
    try {
      setIsLoading(true);
      const newRole = currentRole === 'admin' ? 'user' : 'admin';
      const res = await fetch(`${API_URL}/api/users/${userId}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({ role: newRole })
      });
      const data = await res.json();
      if (data.success) {
        setUsers(users.map(user =>
          user.id === userId
            ? { ...user, role: newRole }
            : user
        ));
      } else {
        setError(data.error || 'Failed to update user role');
      }
    } catch (err) {
      setError('Failed to update user role');
    } finally {
      setIsLoading(false);
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewUser(prev => ({
      ...prev,
      [name]: value
    }));
  };

  if (isLoading) return <div>Loading...</div>;
  if (error) return <div>Error: {error}</div>;

  return (
    <div className="admin-user-management">
      <h1>用戶管理</h1>
      
      <div className="search-bar">
        <input
          type="text"
          placeholder="搜尋用戶..."
          value={searchTerm}
          onChange={handleSearch}
        />
        <button 
          className="add-user-btn"
          onClick={() => setShowAddUser(true)}
        >
          新增用戶
        </button>
      </div>

      {showAddUser && (
        <div className="add-user-form">
          <h2>新增用戶</h2>
          <form onSubmit={handleAddUser}>
            <div className="form-group">
              <label>用戶名：</label>
              <input
                type="text"
                name="name"
                value={newUser.name}
                onChange={handleInputChange}
                required
              />
            </div>
            <div className="form-group">
              <label>密碼：</label>
              <input
                type="password"
                name="password"
                value={newUser.password}
                onChange={handleInputChange}
                required
              />
            </div>
            <div className="form-group">
              <label>角色：</label>
              <select
                name="role"
                value={newUser.role}
                onChange={handleInputChange}
              >
                <option value="user">一般用戶</option>
                <option value="admin">管理員</option>
              </select>
            </div>
            <div className="form-actions">
              <button type="submit" className="submit-btn">新增</button>
              <button 
                type="button" 
                className="cancel-btn"
                onClick={() => setShowAddUser(false)}
              >
                取消
              </button>
            </div>
          </form>
        </div>
      )}

      {editingUser && (
        <div className="edit-user-form add-user-form">
          <h2>編輯用戶</h2>
          <form onSubmit={handleEditSubmit}>
            <div className="form-group">
              <label>用戶名：</label>
              <input
                type="text"
                name="name"
                value={editForm.name}
                onChange={handleEditInputChange}
                required
              />
            </div>
            <div className="form-group">
              <label>新密碼：</label>
              <input
                type="password"
                name="password"
                value={editForm.password}
                onChange={handleEditInputChange}
                placeholder="留空表示不修改密碼"
              />
            </div>
            <div className="form-group">
              <label>角色：</label>
              <select
                name="role"
                value={editForm.role}
                onChange={handleEditInputChange}
              >
                <option value="user">一般用戶</option>
                <option value="admin">管理員</option>
              </select>
            </div>
            <div className="form-actions">
              <button type="submit" className="submit-btn">保存</button>
              <button type="button" className="cancel-btn" onClick={handleEditCancel}>取消</button>
            </div>
          </form>
        </div>
      )}

      <div className="user-list">
        <table>
          <thead>
            <tr>
              <th>用戶名</th>
              <th>角色</th>
              <th>操作</th>
            </tr>
          </thead>
          <tbody>
            {filteredUsers.map(user => (
              <tr key={user.id}>
                <td>{user.name}</td>
                <td>{user.role === 'admin' ? '管理員' : '一般用戶'}</td>
                <td>
                  <button
                    onClick={() => handleToggleAdmin(user.id, user.role)}
                    className={user.role === 'admin' ? 'remove-admin' : 'make-admin'}
                  >
                    {user.role === 'admin' ? '取消管理員' : '設為管理員'}
                  </button>
                  <button
                    onClick={() => handleEditClick(user)}
                    className="edit-user"
                  >
                    編輯
                  </button>
                  <button
                    onClick={() => handleDeleteUser(user.id)}
                    className="delete-user"
                  >
                    刪除
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
} 