import React, { useState } from "react";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import "../assets/css/TaskBoard.css";

const initialTasks = {
  todo: [],
  inProgress: [],
  done: [],
};

const TaskBoard = () => {
  const [columns, setColumns] = useState(initialTasks);
  const [newTask, setNewTask] = useState("");
  const [newTaskCol, setNewTaskCol] = useState("todo");

  const onDragEnd = (result) => {
    const { source, destination } = result;
    if (!destination) return;

    const sourceCol = Array.from(columns[source.droppableId]);
    const destCol = Array.from(columns[destination.droppableId]);
    const [movedTask] = sourceCol.splice(source.index, 1);

    if (source.droppableId === destination.droppableId) {
      sourceCol.splice(destination.index, 0, movedTask);
      setColumns({ ...columns, [source.droppableId]: sourceCol });
    } else {
      destCol.splice(destination.index, 0, movedTask);
      setColumns({
        ...columns,
        [source.droppableId]: sourceCol,
        [destination.droppableId]: destCol,
      });
    }
  };

  // 新增任務
  const handleAddTask = (e) => {
    e.preventDefault();
    if (!newTask.trim()) return;
    const newTaskObj = {
      id: Date.now().toString(),
      content: newTask.trim(),
    };
    setColumns({
      ...columns,
      [newTaskCol]: [...columns[newTaskCol], newTaskObj],
    });
    setNewTask("");
  };

  // 刪除任務
  const handleDeleteTask = (colId, taskId) => {
    setColumns({
      ...columns,
      [colId]: columns[colId].filter((task) => task.id !== taskId),
    });
  };

  return (
    <div className="task-board-container">
      <h2>📝 任務看板</h2>
      {/* 新增任務表單 */}
      <form onSubmit={handleAddTask} style={{ marginBottom: 16, display: "flex", gap: 8 }}>
        <input
          type="text"
          placeholder="輸入新任務內容..."
          value={newTask}
          onChange={(e) => setNewTask(e.target.value)}
          style={{ flex: 1, fontSize: 16, padding: 8 }}
        />
        <select value={newTaskCol} onChange={(e) => setNewTaskCol(e.target.value)} style={{ fontSize: 16, padding: 8 }}>
          <option value="todo">待處理</option>
          <option value="inProgress">進行中</option>
          <option value="done">已完成</option>
        </select>
        <button type="submit" style={{ fontSize: 16, padding: "8px 16px" }}>新增任務</button>
      </form>
      <DragDropContext onDragEnd={onDragEnd}>
        <div className="task-columns">
          {Object.entries(columns).map(([colId, tasks]) => (
            <Droppable droppableId={colId} key={colId}>
              {(provided) => (
                <div
                  className="task-column"
                  ref={provided.innerRef}
                  {...provided.droppableProps}
                >
                  <h3>
                    {colId === "todo"
                      ? "📌 待處理"
                      : colId === "inProgress"
                      ? "🚧 進行中"
                      : "✅ 已完成"}
                  </h3>
                  {tasks.map((task, index) => (
                    <Draggable
                      key={task.id}
                      draggableId={task.id}
                      index={index}
                    >
                      {(provided) => (
                        <div
                          className="task-card"
                          ref={provided.innerRef}
                          {...provided.draggableProps}
                          {...provided.dragHandleProps}
                          style={{ display: 'flex', alignItems: 'center', gap: 8, ...provided.draggableProps.style }}
                        >
                          <span style={{ flex: 1 }}>{task.content}</span>
                          <button
                            onClick={() => handleDeleteTask(colId, task.id)}
                            style={{ background: '#ff4d4f', color: '#fff', border: 'none', borderRadius: 4, padding: '2px 8px', cursor: 'pointer' }}
                            title="刪除任務"
                          >
                            刪除
                          </button>
                        </div>
                      )}
                    </Draggable>
                  ))}
                  {provided.placeholder}
                </div>
              )}
            </Droppable>
          ))}
        </div>
      </DragDropContext>
    </div>
  );
};

export default TaskBoard;
