import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000';

export default function AdminFeedbackManagement() {
  const history = useHistory();
  const [feedbacks, setFeedbacks] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const token = localStorage.getItem('token');

  useEffect(() => {
    const fetchFeedbacks = async () => {
      setIsLoading(true);
      try {
        const res = await fetch(`${API_URL}/api/feedback`, {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        });
        const data = await res.json();
        if (data.success) {
          setFeedbacks(data.feedbacks || []);
        } else {
          setError(data.error || 'Failed to fetch feedback');
        }
      } catch (err) {
        setError('Failed to fetch feedback: ' + err.message);
      } finally {
        setIsLoading(false);
      }
    };
    fetchFeedbacks();
  }, [token]);

  const handleDelete = async (id) => {
    if (!window.confirm('確定要刪除此反饋嗎？')) return;
    setIsLoading(true);
    try {
      const res = await fetch(`${API_URL}/api/feedback/${id}`, {
        method: 'DELETE',
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      const data = await res.json();
      if (data.success) {
        setFeedbacks(feedbacks.filter(fb => fb._id !== id));
      } else {
        setError(data.error || 'Failed to delete feedback');
      }
    } catch (err) {
      setError('Failed to delete feedback: ' + err.message);
    } finally {
      setIsLoading(false);
    }
  };

  if (isLoading) return <div>載入中...</div>;
  if (error) return <div style={{color:'red'}}>Error: {error}</div>;

  return (
    <div style={{padding: '24px'}}>
      <h2>反饋管理</h2>
      <table border="1" cellPadding="8" style={{width: '100%', marginTop: '16px'}}>
        <thead>
          <tr>
            <th>用戶名</th>
            <th>內容</th>
            <th>提交時間</th>
            <th>操作</th>
          </tr>
        </thead>
        <tbody>
          {feedbacks.length === 0 ? (
            <tr><td colSpan="4">暫無反饋</td></tr>
          ) : feedbacks.map(fb => (
            <tr key={fb._id}>
              <td>{fb.username}</td>
              <td>{fb.content}</td>
              <td>{new Date(fb.created_at).toLocaleString()}</td>
              <td>
                <button onClick={() => handleDelete(fb._id)} style={{color:'red'}}>刪除</button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
