import React, { useState } from "react";
import api from '../config/api';

function Project() {
  const [question, setQuestion] = useState("");
  const [answer, setAnswer] = useState("");
  const [likes, setLikes] = useState(0);
  const [loading, setLoading] = useState(false);
  const [hasLiked, setHasLiked] = useState(false);
  const [error, setError] = useState(null);
  const [questionId, setQuestionId] = useState(null);
  const [isSaved, setIsSaved] = useState(false);
  const [category, setCategory] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!question.trim()) {
      alert('請輸入問題');
      return;
    }

    setLoading(true);
    setError('');
    setAnswer('');
    setIsSaved(false);
    setLikes(0);
    setHasLiked(false);
    try {
      // 取得 username，若無則預設 anonymous
      let username = localStorage.getItem('username') || 'anonymous';
      const response = await api.post('/ask', { question, username });
      setAnswer(response.data.answer || '');
      setCategory(response.data.category || ''); // 儲存分類
      setQuestionId(response.data.questionId);
      if (response.data.likes !== undefined) setLikes(response.data.likes);
      // 自動設為已收藏（資料庫已有相同問題時）
      if (response.data.isCollected) {
        setIsSaved(true);
      } else {
        setIsSaved(false);
      }
      if (!response.data.questionId) throw new Error('未收到問題 ID');
      localStorage.setItem('currentQuestionId', response.data.questionId);
      setError(null);
      setQuestion(""); // 提問成功後清空輸入框
    } catch (error) {
      console.error('Error:', error);
      if (error.message !== '未收到問題 ID') {
        setError(error.message || '發生錯誤：請稍後再試');
        alert(error.message || '發生錯誤：請稍後再試');
      }
    } finally {
      setLoading(false);
    }
  };

  const handleLike = async () => {
    try {
      const storedId = questionId || localStorage.getItem('currentQuestionId');
      if (!storedId) {
        setLikes(likes + 1);
        setHasLiked(true);
        alert('此問題尚未收藏，點讚僅暫存於本地');
        return;
      }
      const response = await api.post(`/api/questions/${storedId}/like`);
      if (response.data.success) {
        setLikes(likes + 1);
        setHasLiked(true);
        setError(null);
      } else {
        throw new Error(response.data.error || '點讚失敗');
      }
    } catch (error) {
      console.error('Error liking:', error);
      setError(error.message || '點讚失敗：請稍後再試');
      alert(error.message || '點讚失敗：請稍後再試');
    }
  };


  const handleSave = async () => {
    try {
      if (!answer) throw new Error('沒有可收藏的答案');
      if (isSaved) {
        alert('此問題已收藏到數據庫');
        return;
      }
      const response = await api.post('/collect', {
        question,
        answer,
        category, // 收藏時帶上分類
        likes // 傳送目前的點讚數
      });
      if (response.data && !response.data.error) {
        setIsSaved(true);
        // --- Auto-refresh knowledge base page ---
        // If user is on /knowledge, reload the page. Otherwise, navigate to /knowledge.
        if (window.location.pathname === '/knowledge') {
          window.location.reload();
        } else {
          window.location.href = '/knowledge';
        }
      } else {
        throw new Error(response.data?.error || '收藏失敗');
      }
    } catch (error) {
      console.error('Error saving:', error);
      setError(error.message || '收藏失敗：請稍後再試');
      alert(error.message || '收藏失敗：請稍後再試');
    }
  };

  return (
    <div className="App">
      <h1>AI 知識庫問答系統</h1>

      <form onSubmit={handleSubmit} className="question-box">
        <textarea
          placeholder="請輸入您的問題..."
          value={question}
          onChange={(e) => setQuestion(e.target.value)}
          disabled={loading}
         
        />
        <button type="submit" disabled={loading}>
          {loading ? '正在思考...' : '✨ 提問'}
        </button>
      </form>

      {answer && (
        <div className="answer-box">
          <h2>✨ AI 回答：</h2>
          <p>{answer}</p>
          <div style={{ display: 'flex', gap: '10px', marginTop: '15px' }}>
            <button 
              onClick={() => {
                if (!isSaved) {
                  setLikes(likes + 1);
                  setHasLiked(true);
                  alert('此問題尚未收藏，點讚僅暫存於本地');
                  return;
                }
                handleLike();
              }}
              disabled={hasLiked}
              title={!isSaved ? '此問題尚未收藏，點讚僅暫存於本地' : ''}
            >
              {hasLiked ? '已點讚' : `👍 點讚 (${likes})`}
            </button>
            <button onClick={handleSave} disabled={isSaved || !answer}>
              {isSaved ? '已收藏' : '✨ 收藏到數據庫'}
            </button>
          </div>
        </div>
      )}

      {error && (
        <div style={{ color: 'red', marginTop: '20px' }}>
          {error === '未收到問題 ID' ? null : error}
        </div>
      )}
    </div>
  );
}

export default Project;
