import React, { useEffect, useState } from 'react';

export default function AdminQA({ adminToken }) {
  const [questions, setQuestions] = useState([]);
  const [message, setMessage] = useState('');
  const [searchQuery, setSearchQuery] = useState("");

  useEffect(() => {
    fetch('/api/questions', {
      headers: { Authorization: `Bearer ${adminToken}` }
    })
      .then(res => res.json())
      .then(data => {
        if (data.success) setQuestions(data.questions || data.data || []);
        else setMessage(data.error || '讀取失敗');
      })
      .catch(() => setMessage('連線錯誤'));
  }, [adminToken]);

  const handleSearch = async () => {
    try {
      const params = new URLSearchParams();
      if (searchQuery) params.append('keyword', searchQuery);
      const res = await fetch(`/search?${params.toString()}`, {
        headers: { Authorization: `Bearer ${adminToken}` }
      });
      const data = await res.json();
      if (data.success) setQuestions(data.data || []);
      else setMessage(data.error || '搜尋失敗');
    } catch {
      setMessage('搜尋失敗');
    }
  };

  const handleDelete = async (id) => {
    if (!window.confirm('確定要刪除這個問題嗎？')) return;
    try {
      const res = await fetch(`/api/questions/${id}`, {
        method: 'DELETE',
        headers: { Authorization: `Bearer ${adminToken}` }
      });
      const data = await res.json();
      if (data.success) setQuestions(questions.filter(q => q._id !== id));
      else setMessage(data.error || '刪除失敗');
    } catch {
      setMessage('連線錯誤');
    }
  };

  return (
    <div className="knowledge-base" style={{minHeight: 'calc(100vh - 140px)'}}>
      <h1>問答管理</h1>
      {message && <div style={{ color: 'red' }}>{message}</div>}
      {/* 控制區塊（搜尋、分類） */}
      <div className="knowledge-controls">
        <input
          type="text"
          placeholder="搜尋問題..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          className="search-box"
        />
        <button onClick={handleSearch} style={{marginLeft: '8px'}}>搜尋</button>
      </div>
      <table className="knowledge-table">
        <thead>
          <tr>
            <th>問題</th>
            <th>分類</th>
            <th>讚</th>
            <th>日期</th>
            <th>操作</th>
          </tr>
        </thead>
        <tbody>
          {questions.map(q => (
            <tr key={q._id}>
              <td>{q.question}</td>
              <td>{q.category || '未分類'}</td>
              <td>👍 {q.likes || 0}</td>
              <td>{q.created_at ? new Date(q.created_at).toLocaleDateString() : ''}</td>
              <td>
                <button 
                  onClick={() => handleDelete(q._id)}
                  className="delete-button"
                  style={{
                    backgroundColor: '#dc3545',
                    color: '#fff',
                    border: 'none',
                    padding: '8px 16px',
                    borderRadius: '4px',
                    cursor: 'pointer',
                    fontSize: '14px'
                  }}
                >
                  ✖️ 刪除
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
